.fsection{
    font-size: 1rem;
    display: flex;
    flex-direction: column;
    background: rgb(85, 85, 85);
    height: 100vh;
    width: 100%;
}
.top{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(53, 52, 50);
    height: 8vh;
    color: white;
}
.links{
    color: white;
    font-size: 1.5rem;
    height: 50vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
.para{
    margin: 10px;
}
li{
    margin: 10px;
}
.contact{
    display: flex;
    flex-flow: column;
    align-content: center;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
    top: 40px;
    height: 36vh;
    background-color: rgb(53, 52, 50);
    color: white;
    font-size: 3rem;
    padding: 10px;

}