@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,500;1,400&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html {
    font-size: 62.5%;
    font-family: 'Roboto', sans-serif;
}
li {
    list-style: none;
}

a {
    text-decoration: none;
}
.nav-fix{
    border-bottom: 1px solid #E2E8F0;
    position: fixed;
    top:0px;
    width: 100%;
    background: #fff;
    z-index: 1;
}
.header1{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 1px solid #E2E8F0;
    height: 6vh;
    width: 100%;
    margin-top: 1em;

}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 1.5rem;
}


.hamburger {
    display: none;
}

.bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background-color: #101010;
}


.nav-menu {
    z-index: 1;
    display: flex;
    align-items: center;
}

.nav-item {
    margin-left: 0rem;
}

.nav-link{
    font-size: 1.6rem;
    font-weight: 400;
    color: #475569;
}

.nav-link:hover{
    color: #482ff7;
}

.nav-logo {
    margin: 3px;
    font-size: 2.1rem;
    font-weight: 500;
    color: #482ff7;
    margin: 0px 10px;
}
.logo {
    margin: 0px;
    font-size: 2em;
    font-weight: 500;
    color: #482ff7;
    margin: 0px 10px;
}


