.nav-link.nav-logo{
    margin-top: 30px;
    margin-bottom: 10px;
    border:0.5px solid #101010;
    padding: 15px 0px;
    border-radius: 20px;
    padding: 15px 5px;
    margin: 17px 55px;
}
    .nav-menu.active {
        left: 0;
        border:solid 1px #101010;
    }

    .nav-item {
        margin: 2.5rem 0;
        cursor: pointer;
        padding: 0px 0px;
        box-shadow: 0px 0px 0px 0px;
        padding: 0px auto;
        width: 95%;
    }

    .hamburger {
        z-index: 1;
        display: block;
        cursor: pointer;
    }
    .hamburger.active .bar:nth-child(2) {
        opacity: 0;
    }

    .hamburger.active .bar:nth-child(1) {
        transform: translateY(8px) rotate(45deg);
    }

    .hamburger.active .bar:nth-child(3) {
        transform: translateY(-8px) rotate(-45deg);
    }
/* } */
